/*
 RecordApp: Hybrid App Native

IOSAndroid AppApp

IOSWKWebView(UIWebView)getUserMediaAndroidAndroidjsAndroidIOSIOS

App(Native)JsBridgeh5NativePCM[Int16] 4jsRecorderjsNativeAndroidIOSApp



JsBridge  3Native.Config

app.jsNative.Configapp-support-sampleAndroidIOSdemocopy

https://github.com/xiangyuecn/Recorder
*/
(function(){
"use strict";

var App=RecordApp;
var platform=App.Platforms.Native;
var config=platform.Config;

platform.IsInit=true;

/*******Hybrid App Nativejs*******/
/*
pcmDataBase64: base64<Int16[]> PCMInt16[]base64
sampleRate123456 
*/
var onRecFn=window.NativeRecordReceivePCM=window.top.NativeRecordReceivePCM=function(pcmDataBase64,sampleRate){//iframe
	var rec=onRecFn.rec;
	if(!rec){
		console.error("Native PCM");
		return;
	};
	if(!rec._appStart){
		rec.envStart({
			envName:platform.Key,canProcess:platform.CanProcess()
		},sampleRate);
	};
	rec._appStart=1;
	
	var bstr=atob(pcmDataBase64),n=bstr.length;
	var pcm=new Int16Array(n/2);
	var sum=0;
	for(var idx=0,s,i=0;i+2<=n;idx++,i+=2){
		s=((bstr.charCodeAt(i)|(bstr.charCodeAt(i+1)<<8))<<16)>>16;
		pcm[idx]=s;
		sum+=Math.abs(s);
	};
	
	rec.envIn(pcm,sum);
};


/*******app.js RecordApp*******/

platform.RequestPermission=function(success,fail){
	config.JsBridgeRequestPermission(success,fail);
};
platform.Start=function(set,success,fail){
	onRecFn.param=set;
	var rec=Recorder(set);
	rec.set.disableEnvInFix=true; //
	
	onRecFn.rec=rec;//rec.start
	App.__Rec=rec;//recstop
	
	config.JsBridgeStart(set,success,fail);
};
platform.Stop=function(success,fail){
	var failCall=function(msg){
		fail(msg);
		onRecFn.rec=null;
		App.__Rec=null;
	};
	config.JsBridgeStop(function(){
		var rec=onRecFn.rec;
		onRecFn.rec=null;
		
		if(!rec){
			failCall("");
			return;
		};
		
		console.log("rec encode start: pcm:"+rec.recSize+" src:"+rec.srcSampleRate+" set:"+JSON.stringify(onRecFn.param));
		
		var end=function(){
			//
			for(var k in rec.set){
				onRecFn.param[k]=rec.set[k];
			};
		};
		if(!success){
			end();
			failCall("");
			return;
		};
		rec.stop(function(blob,duration){
			console.log("rec encode end")
			end();
			success(blob,duration);
			App.__Rec=null;
		},function(msg){
			end();
			failCall(msg);
		});
	},failCall);
};
})();